<template>
    <div id="evaluate-project">
        <head-top></head-top>
        <top-nav></top-nav>
        <div class="main-container">
            <project-list ref="projectlist" @totalCnt="getTotalCnt"></project-list>
            <div class="nav-right view row-container">
                <div class="page-title" v-show="Number(totalCnt)!=0">
                    评估{{project.name}}
                    <el-tooltip class="item" effect="dark" content="编辑" placement="top-start">
                        <el-button type="text" icon="el-icon-edit" class="margin-left" @click="editClick"></el-button>
                    </el-tooltip>
                    <el-tooltip class="item" effect="dark" content="删除" placement="top-start">
                        <el-button type="text" icon="el-icon-delete" class="margin-left" @click="deleteClick"></el-button>
                    </el-tooltip>

                </div>
                <div class="margin-top-big" v-show="Number(totalCnt)!=0">
                    <el-row>
                        <el-col :span="2">项目名称:</el-col>
                        <el-col :span="6">{{project.name ? project.name : '&nbsp'}}</el-col>
                        <el-col :span="2">项目地点:</el-col>
                        <el-col :span="6">{{project.location ? project.location : '&nbsp'}}</el-col>
                        <el-col :span="2">投入时间:</el-col>
                        <el-col :span="6">{{project.beginTime | dateFormatterFilter}}</el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="2">建筑面积:</el-col>
                        <el-col :span="6">{{project.area}}m<sup>2</sup></el-col>
                        <el-col :span="2">建筑类型:</el-col>
                        <el-col :span="6">{{project.type}}</el-col>
                    </el-row>
                    <!--<div class="margin-bottom"><span class="ieds-dot"></span>特征值</div>
                    <el-radio-group v-model="season" @change="seasonChange">
                        <el-radio :label="1">供冷季</el-radio>
                        <el-radio :label="0">供暖季</el-radio>
                    </el-radio-group>
                       <el-radio v-model="radio" label="1">供暖季</el-radio>
                        <el-radio v-model="radio" label="2">供冷季</el-radio>-->
                </div>

                <div class="param-mark">
                    <charts></charts>
                    <!--<paramMark></paramMark>
                     <eerparamMark></eerparamMark>-->
                </div>
            </div>
        </div>
        <!--<footer-bottom></footer-bottom>-->
    </div>
</template>
<script>
    import headTop from '../../Header.vue'
    import footerBottom from '../../Footer.vue'
    import topNav from '../../TopNav.vue'
    import projectList from './ProjectList.vue'
    import charts from './Charts.vue'

    export default {
        data() {
            return {
                id: '',
                project: {},
                totalCnt:'',
                // season: 0
                // mark: true
            }
        },
        components: {
            headTop,
            footerBottom,
            topNav,
            projectList,
            charts
            // paramMark,
            // eerparamMark
        },

        watch: {
            '$route'(to, from){
                this.getItem();
            }
        },

        created() {
            this.id = this.$route.query.id;
            this.getItem();
        },

        methods: {
            getTotalCnt(totalCnt){
                this.totalCnt = totalCnt ;
            },
            getItem(){

                let self = this;
                let url = GSHP_SERVER_ENDPOINT + '/project/projectInfo';
                self.$http.get(url, {
                    params: {
                        id: self.$route.query.id
                    }
                }).then((response) => {
                    let data = response.data;
                    let obj = {
                        name: data.project_name,
                        location: data.position,
                        beginTime: data.begin_time,
                        area: data.area,
                        type: data.type
                    };
                    this.project = obj;
                });

            },

            editClick(){
                this.$refs.projectlist.projectAdd(true);
            },

            deleteClick() {
                var self = this;
                this.$confirm('此操作将永久删除该项目以及该项目的所有参数信息, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    var url = GSHP_SERVER_ENDPOINT + '/projects?id=' + self.$route.query.id;
                    self.$http.delete(url)
                        .then(function (response) {
                            if (response.status === 200) {
                                self.$message({
                                    type: 'success',
                                    message: '删除项目"' + self.project.name + '"成功!'
                                });
                                self.$router.push({path: '/home'});
                            }
                        })
                        .catch(function (error) {
                            self.$message({
                                type: 'error',
                                message: '删除失败!'
                            });
                        });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },

            /*renderHeader1(createElement, {column, $index}) {
             return createElement(
             'span',
             {
             'class': ''
             },
             ['t',
             createElement('sub', {
             attrs: {type: 'text'},

             }, ['g,o']
             )
             ]
             );
             },

             renderHeader2(createElement, {column, $index}) {
             return createElement(
             'span',
             {
             'class': ''
             },
             ['∆t',
             createElement('sub', {
             attrs: {type: 'text'},

             }, ['g']
             )
             ]
             );
             },*/

            seasonChange(val){

            },


        }
    }
</script>
<style>
    /* .ieds-dot {
         display: inline-block;
         height: 12px;
         width: 12px;
         border-radius: 6px;
         background-color: #409EFF;
         margin-right: 15px;
     }*/

    /*#evaluate-project .el-row {
        margin-left: 28px;
    }*/
    #evaluate-project .el-radio {
        /*margin-left: 28px;*/
        margin-top: 10px;
        margin-bottom: 30px;
    }
</style>


